import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import * as emailJS from 'emailjs-com';
import { environment } from 'src/environments/environment';
import { SharedService } from 'src/app/services/shared.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
  contactForm: FormGroup;

  constructor(
    private sharedService: SharedService,
    private toastr: ToastrService
  ) {}

  ngOnInit(): void {
    this.initForm();
    emailJS.init(environment.userID, this.contactForm.value.email);
  }

  initForm(): void {
    this.contactForm = new FormGroup({
      name: new FormControl('', Validators.required),
      email: new FormControl('', Validators.email),
      message: new FormControl('', Validators.required)
    });
  }

  onSubmit(): void {
    this.sharedService.emitNewRequestStatus(true);
    emailJS
      .send('outlook', 'portfolio_page', this.contactForm.value)
      .then(res => {
        this.sharedService.emitNewRequestStatus(false);
        this.toastr.success('Your message has been sent!', 'Thank you!');
        this.contactForm.reset();
      })
      .catch(err => {
        this.sharedService.emitNewRequestStatus(false);
        this.toastr.error(err.message, 'Oops! An error occurred..');
      });
  }
}
