import { Component, OnInit } from '@angular/core';
import * as AOS from 'aos';
import { trigger, transition, style, animate } from '@angular/animations';
import { SharedService } from './services/shared.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [
    trigger('enterAnimation', [
      transition(':enter', [
        style({ opacity: 0 }),
        animate('200ms', style({ opacity: 1 }))
      ]),
      transition(':leave', [
        style({ opacity: 1 }),
        animate('200ms', style({ opacity: 0 }))
      ])
    ])
  ]
})
export class AppComponent implements OnInit {
  title = 'Antonij Djajkoski';
  displayTop = 0;
  outgoingRequest = false;
  notificationIncoming = false;
  notification: boolean;

  constructor(private sharedService: SharedService) {}

  ngOnInit(): void {
    window.addEventListener('scroll', this.scrollEvent, true);
    // Init AOS
    AOS.init({
      once: true,
      delay: 0,
      duration: 1000
    });

    // Shared Service Subjects
    this.sharedService.requestStatus.subscribe(res => {
      if (res === true) {
        this.outgoingRequest = true;
      } else {
        this.outgoingRequest = false;
      }
    });
  }

  scrollEvent = (event: any): void => {
    this.displayTop = event.currentTarget.pageYOffset;
  };

  scrollToTop(): void {
    window.scrollTo(0, 0);
  }
}
