import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SharedService {
  requestStatus: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);

  constructor() {}

  emitNewRequestStatus(value: boolean): void {
    this.requestStatus.next(value);
  }
}
